import { model, Schema, models } from 'mongoose';

export default function getshipmentsModel() {
  const collectionName = 'shipments';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const shipmentsSchema = new Schema({
    consignmentType: {
      type: String,
      required: true,
    },
    destination: {
      type: String,
    },
    vehicleNumber: {
      type: String,
    },
    isssueTime: {
      type: Date,
      required: true,
    },
    date: {
      type: Date,
      required: true,
    },
    packingList: {
      type: String,
      required: true,
    },
    evc: {
      type: String,
    },
    grn: {
      type: String,
    },
    status: {
      type: String,
    },
  });

  return model(collectionName, shipmentsSchema);
}
